import React, { useContext, useEffect } from 'react'
import { ItemContext } from '../contexts/ItemContext'

export default function Enlarge (props) {
  const { zoom, setZoom } = useContext(ItemContext)

  function toggleZoom () {
    if (!props.disabled) {
      setZoom(!zoom)
    }
  }

  return (
    <div
      style={{
        borderRadius: 100,
        width: props.width ? props.width : 50,
        // marginLeft: 10,
        // marginBottom: 20,
        // textAlign: 'center',
        height: props.height ? props.height : 50,
        // display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        position: props.position || 'absolute',
        top: props.top ? props.top : 0,
        display: props.showStatus ? props.showStatus === 'show' ? 'flex' : 'none' : 'initial',
        right: props.right || 0,
        zIndex:10
      }}
      onClick={toggleZoom}
    >
      <span>
        {zoom
          ? <img
            src={require('../assets/Circle_Ic_Narrow@3x.png')}
            alt=''
            style={{
              width: '100%',
              height: '100%'
            }}
          /> : <img
            src={require('../assets/Circle_Ic_Enlarge@3x.png')}
            alt=''
            style={{
              width: '100%',
              height: '100%'
            }}
          />}
      </span>
    </div>
  )
}
