import React, { useState, useEffect, useContext, useRef, useReducer } from 'react';
import { ItemContext } from '../contexts/ItemContext';
import loadImages from '../utils/loadImages';
import { ImagesReducer } from '../utils/ImagesReducer';
import imageTools from '../utils/imageTools';

export default function (props) {
  const canvasRef = useRef();
  const [images, dispatch] = useReducer(ImagesReducer, []);
  const { src, background, canvasSize, style, canvasArea, isTopLayer, multiply } = props;
  const className = isTopLayer ? 'layerHierarchy' : null;
  const { width, height } = canvasArea;
  useEffect(() => {
    const { current } = canvasRef;
    if (current && images.length) {
      const [back, front] = images;
      imageTools.multiplyImage(back, front, canvasArea, canvasSize, current, isTopLayer);
    }
  }, [canvasRef, images]);

  useEffect(() => {
    loadImages(background, src).then(textures => dispatch({ type: 'ready', textures }));
  }, [background, src]);

  if (multiply) {
    return <canvas ref={canvasRef} width={width} height={height} style={style} className={className} />;
  } else {
    return <img src={src} style={style} className={className} alt="" />;
  }
}
