import React, { useContext } from 'react';
import { ItemContext } from '../contexts/ItemContext';
import './Viewport.css';
import MultiplyImage from './MultiplyImage';

export default function ViewPort(props) {
  const { multiply, doneMultiply, canvasArea, canvasSize, areaStyle, doneStyle, imageStyle, doneImageStyle, scale, doneScale, translateX, translateY, doneTranslateX, doneTranslateY, screenTopOffset, zoom, doneImageSrc, imageSrc } = useContext(ItemContext);
  const { size, topLayer, data, noMultiply, ...restProps } = props;
  let localeScale = scale;
  let localeTranslateY = translateY;
  let localeTranslateX = translateX;
  if (size) {
    localeTranslateY = doneTranslateY;
    localeTranslateX = doneTranslateX;
    if (zoom) {
      localeScale = doneScale;
    } else {
      const { width, height } = doneImageStyle;
      localeScale = size / width;
      localeTranslateY = (55 - screenTopOffset - height * (scale - localeScale) / 2 ) / localeScale;
    }
  }
  const style = {
    ...(size ? doneImageStyle : imageStyle),
    transform: `scale(${localeScale})${localeTranslateY ? ` translateY(${localeTranslateY}px)` : ''}${localeTranslateX ? ` translateX(${localeTranslateX}px)` : ''}`,
  };
  const background = size ? doneImageSrc : imageSrc;
  const multiplyProps = {
    background,
    canvasSize,
    canvasArea,
    style: size ? doneStyle : areaStyle,
    multiply: /*noMultiply ? false : */size ? doneMultiply : multiply,
  };
  return (
    <div className="viewport" style={style} {...restProps}>
      <img className='imgBackground' src={background} alt='' />
      {data && <MultiplyImage src={data} {...multiplyProps} />}
      {props.children}
      {topLayer && <MultiplyImage isTopLayer src={topLayer} {...multiplyProps} />}
    </div>
  );
}
