import React, { useCallback, useContext, useEffect, useRef, useState } from 'react'
import Navbar from '../navbar'
import Toolbar from '../toolbar'
import { ItemContext } from '../../contexts/ItemContext'
import { MakeContext } from '../MakeContext'
import RevokeComponent from '../../components/revoke'
import ViewPort from '../../components/ViewPort'
import Enlarge from '../../components/Enlarge'
import ColorGroup from './colorGroup'
import BrushGroup from './brushGroup'
import GoToNext from '../../components/GoToNext'
import requestAnimationFrame from '../../utils/requestAnimationFrame'
import cancelAnimationFrame from '../../utils/cancelAnimationFrame'
import { stageJS, uniqlo } from '../../metalib'
import img1 from '../../assets/texture/01.png'
import img2 from '../../assets/texture/02.png'
import img4 from '../../assets/texture/04.png'
import img5 from '../../assets/texture/05.png'
import img6 from '../../assets/texture/06.png'
import img7 from '../../assets/texture/07.png'
import loadImages from '../../utils/loadImages'
import invoke from '../../utils/invoke'
import instruction from '../../utils/instruction'
import constants from '../../constants/instruction'
import Locale from '../../components/Locale'
import { LocaleContext } from '../../contexts/LocaleContext'
import injectImages from '../../hoc/injectImages'
import AlertModal from '../../components/AlertModal'
import imageTools from '../../utils/imageTools'

function getEventRect (t) {
  const { touches, target } = t
  const { left, top, width, height } = target.getBoundingClientRect()
  if (touches) {
    const touch = touches[0]
    if (touch) {
      return [touch.clientX - left, touch.clientY - top, width, height]
    }
  }
  return [t.clientX - left, t.clientY - top, width, height]
}

function getXY (t) {
  const [x, y, width, height] = getEventRect(t)
  return {
    x: x / width * uniqlo.UTItemManager.IMAGE_WIDTH,
    y: y / height * uniqlo.UTItemManager.IMAGE_HEIGHT
  }
}

const style = {
  position: 'absolute',
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
  overflow: 'hidden',
  userSelect: 'none'
}

function DrawArea (props) {
  const { areaStyle, setZoom, canvasArea: { width: canvasWidth, height: canvasHeight } } = useContext(ItemContext)
  const { layers, brushColor, setBrushColor, appid, previousPage } = useContext(MakeContext)
  const languageType = useContext(LocaleContext)
  const canvasRef = useRef()
  const [showStatus, setShowStatus] = useState(false)
  const [selectedIndex, _setSelectedIndex] = useState(0)
  const [colorSelectMode, setColorSelectMode] = useState(false)
  const [brushMode, setBrushMode] = useState(uniqlo.AppVals.SELECTED_TOOL_MODE)
  const [isNew, setIsNew] = useState(true)
  const [images, setImages] = useState([])
  const [firstVisited, setFirstVisited] = useState('')
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const [control, setControl] = useState()

  function setSelectedIndex (i) {
    invoke('setSelectedIndex', i)
    _setSelectedIndex(i)
  }

  useEffect(() => {
    if (control && control.selectedColor !== selectedIndex) {
      control.setColorIndex(selectedIndex)
    }
  }, [control, selectedIndex])

  const selectedColorFu = useCallback(function selectedColorFu (i) {
    setSelectedIndex(i)
    setColorSelectMode(i === selectedIndex ? !colorSelectMode : true)
  }, [colorSelectMode, selectedIndex])

  const change = useCallback(function change () {
    setShowStatus(!showStatus)
  }, [showStatus])
  const stringify = useCallback(async function stringify () {
    const { current } = canvasRef
    if (current) {
      imageTools.storeImageData({ imageName: `front-${layers.length}`, imageData: current.toDataURL() })
      try {
        await invoke('setLayers', layers)
        invoke('navigateTo', {
          to: '/mixChoice',
          params: { itemModal: 'show', previousPage }
        })
      } catch (error) {
        setAlertModelVisible(true)
      }
    }
  }, [canvasRef, layers])

  const cancelDesign = useCallback(function cancelDesign () {
    if (control) {
      control.clear()
      // control.dirty = true;
      // control.onEnterFrame(null);
      setIsNew(true)
    }
  }, [control])

  useEffect(() => {
    loadImages(img1, img2, img4, img5, img6, img7).then(setImages)
  }, [])

  useEffect(() => {
    async function init () {
      try {
        const newSelectedIndex = await invoke('getSelectedIndex')
        const newBrushColor = await invoke('getBrushColor')
        if (newBrushColor.length > 0) {
          setBrushColor(newBrushColor)
        }
        if (!isNaN(newSelectedIndex) && newSelectedIndex !== -1) {
          _setSelectedIndex(newSelectedIndex)
        }

        let visited = await instruction.checkInstruction(constants.Paint_Page_Instruction)
        if (!visited) {
          setFirstVisited('Y')
          await instruction.setInstructionVisited(constants.Paint_Page_Instruction)
          setTimeout(() => {
            setFirstVisited('N')
          }, 3000)
        } else {
          setFirstVisited('N')
        }
      } catch (error) {
        setAlertModelVisible(true)
      }
    }

    init()
  }, [])

  useEffect(() => {
    if (control) {
      control.colorSelectMode = colorSelectMode
    }
  }, [control, colorSelectMode])

  useEffect(() => {
    const { AppVals } = uniqlo
    AppVals.SELECTED_TOOL_MODE = brushMode
    if (control) {
      control.setEraseMode(brushMode === AppVals.ERASE_MODE)
    }
  }, [brushMode, control])

  useEffect(() => {
    if (control) {
      control.setColor(brushColor)
      cancelDesign()
    }
  }, [brushColor, control])

  useEffect(() => {
    const { current: canvas } = canvasRef
    if (canvas && images.length) {
      const [a, b, c, d, e, f] = images
      const { height, width } = areaStyle
      const { AppVals, UTItemManager } = uniqlo
      const stage3d = new stageJS.Stage3D(canvas)
      // AppVals.stage = stage3d;
      AppVals.TEXTURE_WIDTH = width
      UTItemManager.IMAGE_HEIGHT = canvasHeight
      UTItemManager.IMAGE_WIDTH = canvasWidth

      const p1 = stageJS.BitmapData.fromImageElement(a)
      const p2 = stageJS.BitmapData.fromImageElement(b)
      const p4 = stageJS.BitmapData.fromImageElement(c)
      const p5 = stageJS.BitmapData.fromImageElement(d)
      const p6 = stageJS.BitmapData.fromImageElement(e)
      const p7 = stageJS.BitmapData.fromImageElement(f)

      const control = new uniqlo.PaintImpl(p1, p2, null, p4, p5, p6, p7)
      setControl(control)
      control.mFudeSize = 0.6
      control.CBcolorChanged = async function () {
        setSelectedIndex(control.selectedColor)
      }
      control.onPainted = function () {
        if (isNew) {
          setIsNew(false)
        }
      }
      // control.setColor(brushColor);

      control.initWithData(stage3d, null, null)
      control.startRendering()
      control.onEnterFrame(null)

      let animateId = null
      let drawing = false

      function animate () {
        try {
          control.onEnterFrame(null)
          animateId = requestAnimationFrame(animate)
        } catch (e) {
          console.error(e)
        }
      }

      function handleMouseDown (e) {
        const { x, y } = getXY(e)
        control.onMousePress(x, y)
        // canvas.addEventListener('mousemove', handleMouseMove, false);
        canvas.addEventListener('touchmove', handleMouseMove, false)
        // document.addEventListener('mouseup', handleMouseUp, false);
        document.addEventListener('touchend', handleMouseUp, false)
        animate()
      }

      function handleMouseMove (e) {
        drawing = true
        if (e.currentTarget === canvas) {
          const { x, y } = getXY(e)
          control.onMouseDrag(x, y)
        }
      }

      function handleMouseUp (e) {
        if (drawing) {
          control.onMouseRelease()
          drawing = false

          function delayCancel () {
            const { mParticles } = control
            if (!mParticles || !mParticles.length) {
              if (animateId) {
                cancelAnimationFrame(animateId)
              }
            } else {
              requestAnimationFrame(delayCancel)
            }
          }

          delayCancel()
        } else if (animateId) {
          cancelAnimationFrame(animateId)
        }
        // canvas.removeEventListener('mousemove', handleMouseMove, false);
        canvas.removeEventListener('touchmove', handleMouseMove, false)
        // document.removeEventListener('mouseup', handleMouseUp, false);
        document.removeEventListener('touchend', handleMouseUp, false)
      }

      setTimeout(() => setZoom(true), 800)

      let GL

      function handleStateChange ({ type, prevAppid, nextAppid }) {
        if (type === 'push' && prevAppid === appid) {
          GL = stageJS.Context3D.GL
        }
        if (type === 'pop' && nextAppid === appid && GL) {
          stageJS.Context3D.GL = GL
        }
      }

      // canvas.addEventListener('mousedown', handleMouseDown, false);
      canvas.addEventListener('touchstart', handleMouseDown, false)
      global.AppManager.appRouterEvent.addEventListener('UTmeStateChange', handleStateChange)
      return () => {
        control.dispose()
        // canvas.removeEventListener('mousedown', handleMouseDown, false);
        canvas.removeEventListener('touchstart', handleMouseDown, false)
        // canvas.removeEventListener('mousemove', handleMouseMove, false);
        canvas.removeEventListener('touchmove', handleMouseMove, false)
        // document.removeEventListener('mouseup', handleMouseUp, false);
        document.removeEventListener('touchend', handleMouseUp, false)
        global.AppManager.appRouterEvent.removeEventListener('UTmeStateChange', handleStateChange)
      }
    }
  }, [canvasRef, images])

  function revertColor (color) {
    let _color = color.toString(16)
    while (6 - _color.length) {
      _color = '0' + _color
    }
    return `#${_color}`
  }

  const classes = ['paintColorList']

  if (colorSelectMode) {
    classes.push('selectColorMode')
  }

  function resetColorIndex () {
    setSelectedIndex(0)
  }

  return (
    <div style={style}>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => {
        setAlertModelVisible(false)
      }} />
      <div style={{ display: firstVisited === 'N' ? 'block' : 'none' }}>
        <Navbar page='paint' revoke={change}>
          <GoToNext onClick={stringify} disabled={isNew} />
        </Navbar>
        <ViewPort topLayer={props.topLayer} data={props.mixData} noMultiply>
          <div style={{ position: 'absolute', top: 0, left: 0 }}>
            <canvas
              ref={canvasRef}
              width={canvasWidth}
              height={canvasHeight}
              style={areaStyle}
            />
          </div>
        </ViewPort>
        {
          brushMode !== uniqlo.AppVals.ERASE_MODE && (
            <div className={classes.join(' ')}>
              {brushColor.map((color, index) => {
                // const vColor = '#' + color.toString(16).padStart(6, '0');
                const vColor = revertColor(color)
                return [
                  <div
                    key={vColor}
                    className={index === selectedIndex ? 'active' : ''}
                    onClick={() => {
                      selectedColorFu(index)
                    }}
                    style={{
                      backgroundColor: vColor
                    }}
                  />,
                  index !== brushColor.length - 1 ? <span key={`${vColor}-link`} /> : null
                ]
              })}
            </div>
          )
        }
        <Toolbar>
          {brushMode !== uniqlo.AppVals.ERASE_MODE && <ColorGroup resetColorIndex={resetColorIndex} />}
          <BrushGroup brushMode={brushMode} setBrushMode={setBrushMode} />
          <Enlarge />
        </Toolbar>
        <RevokeComponent pageMark='paint' cancelDesign={cancelDesign} show={showStatus} setShow={setShowStatus} />
      </div>
      <div onClick={() => {
        setFirstVisited('N')
      }} style={{
        display: firstVisited === 'Y' ? 'flex' : 'none',
        position: 'fixed',
        left: 0,
        right: 0,
        bottom: 0,
        top: 0,
        overflow: 'hidden',
        backgroundColor: '#fff',
        textAlign: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        zIndex: 10,
        whiteSpace: 'pre'
      }}>
        <img width={280} height={236} style={{ marginLeft: '42%' }} src={require('../../assets/Paint_anime.gif')} />
        <div style={{
          lineHeight: '24px',
          marginTop: 6,
          width: '75%',
          fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
          fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
          fontSize: languageType.current === 'ja' ? '17px' : '20px',
          color: '#1b1b1b'
        }}>
          <Locale code='make.paint.instruction' />
        </div>
      </div>
    </div>
  )
}

export default injectImages()(DrawArea)
