import React, { useContext, useState, useEffect, useMemo } from 'react'
import { MakeContext } from '../MakeContext'
import Locale from '../../components/Locale'
import VBColorToHEX from './helpers/VBColorToHEX'
import brushColors from './helpers/brushColors'
// import textFormat from '../../assets/textFormat.png'
// import leftAlign from '../../assets/leftAlign.png'
// import rightAlign from '../../assets/rightAlign.png'
import './paintTool.css'
import Popup from '../../components/Popup'
import invoke from '../../utils/invoke'
import { LocaleContext } from '../../contexts/LocaleContext'

export default function colorGroup (props) {
  const make = useContext(MakeContext)
  const [iconVisible, setIconVisible] = useState(false)
  const languageType = useContext(LocaleContext)

  const [popupVisible, setPopupVisible] = useState(false)
  const {
    brushColor,
    setBrushColor
  } = make

  const colorGroupIndex = useMemo(() => {
    const index = brushColors.findIndex((ele, index) => JSON.stringify(ele) === JSON.stringify(brushColor))
    return index === -1 ? 0 : index;
  }, brushColor);

  async function changeBrushColorFu (ele, index, { closePopup }) {
    props.resetColorIndex()
    setBrushColor(ele)
    setIconVisible(iconVisible)
    try {
      await invoke('setBrushColor', ele)
    } catch (error) {
      invoke('setBrushColor', ele)
    }
    // invoke('log', 'brushColor' + brushColor);
    closePopup()
  }

  return (
    <Popup
      iconVisible={iconVisible}
      setIconVisible={setIconVisible}
      //   onVisibleChange={(e) => { props.popupchangeAlignment(e) }}
      renderOverlay={(overlayProps) => {
        // console.log(overlayProps)
        const { triggerRect } = overlayProps
        const { closePopup } = overlayProps
        const { visible } = overlayProps
        setPopupVisible(visible)
        if (!triggerRect) return <div />
        let bottom = 102
        return (
          <div style={{ position: 'absolute', zIndex: '11', backgroundColor: '#FFF', width: window.innerWidth, bottom }}>
            {/* 三角形 */}
            <div style={{ position: 'absolute', bottom: -26, left: 26 }}>
              <svg width='30' height='24' viewBox='0 0 30 24'>
                <path
                  fill='#fff'
                  d='M30 0 L15 20 L 0 0 z'
                />
              </svg>
            </div>
            <div style={{
              height: 56,
              width: '100%',
              textAlign: 'center',
              borderBottom: '1px solid #a09e9ed1',
              lineHeight: '56px',
              color: '#1b1b1b',
              fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
              fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
              fontSize: languageType.current === 'ja' ? '17px' : '20px'
            }}>
              {/* 请选择调色板 */}
              <Locale code='make.paint.colorGroup.title' />
            </div>
            <div
              style={{ height: window.innerHeight * 0.6281 + 12 }}
              className='colorBarBox'
            >
              {brushColors.map((element, index) =>
                <div key={index} className='colorBar'
                  onClick={() => changeBrushColorFu(element, index, { closePopup })}
                  style={{ border: index === colorGroupIndex ? `8px solid #1b1b1b` : '0px' }}>
                  {
                    element.map(item =>
                      <div key={item} style={{ backgroundColor: VBColorToHEX(item), float: 'left', width: `${100 / element.length}%`, height: '100%' }} />
                    )
                  }
                </div>
              )}
            </div>
            <div style={{ height: '11px', backgroundColor: '#fff', width: '100%' }} />
          </div>
        )
      }}
    >
      <div className={`colorMenu ${iconVisible ? 'activeIcon' : ''}`} style={{ zIndex: popupVisible ? 13 : 11 }}>
        <img src={require(`../../assets/Color_Normal_0${colorGroupIndex + 1}.svg`)} />
      </div>

    </Popup>
  )
}
