import React, { useContext, useEffect, useState } from 'react'
import Locale from '../../components/Locale'
import './paintTool.css'
import Popup from '../../components/Popup'
import { uniqlo } from '../../metalib'
import { LocaleContext } from '../../contexts/LocaleContext'

export default function brushGroup (props) {
  const languageType = useContext(LocaleContext)
  const [iconVisible, setIconVisible] = useState(false)
  const [popupVisible, setPopupVisible] = useState(false)

  const { setBrushMode, brushMode } = props

  function changeBrushMode (mode, { closePopup }) {
    setBrushMode(mode)
    closePopup()
    setIconVisible(!iconVisible)
  }

  return (
    <Popup
      iconVisible={iconVisible}
      setIconVisible={setIconVisible}
      //   onVisibleChange={(e) => { props.popupchangeAlignment(e) }}
      renderOverlay={(overlayProps) => {
        // console.log(overlayProps)
        const { triggerRect } = overlayProps
        const { closePopup } = overlayProps
        const { visible } = overlayProps
        setPopupVisible(visible)
        if (!triggerRect) return <div />
        let bottom = 102
        return (
          <div style={{ position: 'absolute', zIndex: '11', backgroundColor: '#FFF', width: window.innerWidth, bottom }}>
            {/* 三角形 */}
            <div style={{ position: 'absolute', bottom: -27, left: '50%', marginLeft: -15 }}>
              <svg width='30' height='24' viewBox='0 0 30 24'>
                <path
                  fill='#fff'
                  d='M30 0 L15 20 L 0 0 z'
                />
              </svg>
            </div>
            <div style={{
              height: 56,
              width: '100%',
              textAlign: 'center',
              borderBottom: '1px solid #dadada',
              lineHeight: '56px',
              color: '#1b1b1b',
              fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
              fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
              fontSize: languageType.current === 'ja' ? '17px' : '20px'
            }}>
              <Locale code='make.paint.brushGroup.title' />
            </div>
            <div style={{ width: '100%' }}>
              <div className='brushBar' onClick={() => changeBrushMode(uniqlo.AppVals.THICK_BRUSH_MODE, { closePopup })}>
                <img src={require('../../assets/icon-brush.svg')} />
                <span style={{
                  color: '#1b1b1b',
                  fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                  fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                  fontSize: languageType.current === 'ja' ? '17px' : '20px'
                }}>
                  <Locale code='make.paint.brushGroup.crude.content' />
                </span>
                <img src={require('../../assets/arrow2.svg')} />
              </div>
              <div className='brushBar' onClick={() => changeBrushMode(uniqlo.AppVals.THIN_BRUSH_MODE, { closePopup })}>
                <img src={require('../../assets/thin-brush.svg')} />
                <span style={{
                  color: '#1b1b1b',
                  fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                  fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                  fontSize: languageType.current === 'ja' ? '17px' : '20px'
                }}>
                  <Locale code='make.paint.brushGroup.fine.content' />
                </span>
                <img src={require('../../assets/arrow2.svg')} />
              </div>
              <div className='brushBar' onClick={() => changeBrushMode(uniqlo.AppVals.ERASE_MODE, { closePopup })}>
                <img src={require('../../assets/eraser.svg')} />
                <span style={{
                  color: '#1b1b1b',
                  fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                  fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                  fontSize: languageType.current === 'ja' ? '17px' : '20px'
                }}>
                  <Locale code='make.paint.brushGroup.clear.content' />
                </span>
                <img src={require('../../assets/arrow2.svg')} />
              </div>
            </div>
          </div>
        )
      }}
    >
      {/* <div className={classes.join(' ')} /> */}
      <div className={`brushMenu ${iconVisible ? 'activeIcon' : ''}`} style={{ zIndex: popupVisible ? 13 : 11 }}>
        <img src={require(`../../assets/${brushMode}_normal.svg`)} />
      </div>
    </Popup>
  )
}
